@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&family=Outfit:wght@100;200;300;400;500;600;700;800;900&display=swap');

/*
  <Eng Font>
  font-family: 'Outfit', sans-serif;
  font-family: 'Montserrat', sans-serif;
*/

:root {
  --primary: #45c485;
  --second: #abd3c0;
  --third: #2ba769;
  --sub: #a4a4ac;
  --enFont: 'Montserrat', sans-serif;

  /* Border Set */
  --borderColor: #2e2e31;

  /* Background Set */
  --background: #1f1f20;
}

body {
  color: #ecf1ef;
  background: #1f1f20;
}

* {
  font-family: 'Pretendard-Regular';
  margin: 0;
  padding: 0;
  font-size: 13px;
  outline: none;
  box-sizing: border-box;
  -webkit-text-size-adjust: none;
  -webkit-user-select: none;
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  letter-spacing: -0.3px;
}

ul {
  list-style: none;
}

input,
textarea,
button,
select {
    letter-spacing: normal;
    width: 100%;
    height: 35px;
    padding-left: 10px;
    transition: all 0.3s ease-out;
    -webkit-transition: all 0.3s ease-out;
    -moz-transition: all 0.3s ease-out;
    -ms-transition: all 0.3s ease-out;
    -o-transition: all 0.3s ease-out;
}

select:focus {
  border: 2px solid var(--primary);
  box-shadow: 0px 3px 5px rgba(69, 196, 133, 0.397);
}

textarea {
  -webkit-appearance: none;
}

select {
  -webkit-appearance: menulist-text;
  background: none;
  border: none;
  color: #ecf1ef;
}

select option {
  border: none;
  outline: none;
  background: var(--background);
  line-height: 26px;
}

hr {
  margin: 10px 0px 10px 0px;
  border: 1px solid var(--borderColor);
}